<template>
  <section class="search">
    <!--  -->
    <div class="nav c-gap-inner-left-20">
      <div
        v-for="(item, index) in searchTab.list"
        :key="index"
        class="nav-item c-btn transition-animate"
        :class="{
          'selected': searchTab.curSelectedIndex === index
        }"
        @click="handleTagsClick({
          item,
          index
        })">
        {{item.text}}
      </div>
    </div>
    <!--  -->
    <el-row
      class="search-form">
      <el-col
        v-for="(item, index) in searchList"
        :key="index" 
        :span="8">
        <!--  -->
        <el-select 
          v-model="item.value" 
          placeholder=""
          :style="{
            'width': '100%'
          }">
          <el-option
            v-for="(item2, index2) in item.options"
            :key="index2"
            :label="item2.value"
            :value="item2.value">
          </el-option>
        </el-select>
      </el-col>
      <!--  -->
      <el-col
        :span="8">
        <el-button 
          type="success"
          :style="{
            'width': '100%'
          }"
          @click="handleSearchClick">
          
        </el-button>
      </el-col>
    </el-row>
  </section>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      searchTab: {
        curSelectedIndex: 0,
        list: [
          {
            text: ""
          }
        ]
      },
      searchList: [
        // 
        {
          key: "type",
          text: "",
          value: "&",
          options: [
            {
              value: "&"
            },
            {
              value: ""
            },
            {
              value: ""
            },
            {
              value: ""
            }
          ]
        },
        // 
        {
          key: "model",
          text: "",
          value: "",
          options: [
            {
              value: ""
            },
            {
              value: "1"
            },
            {
              value: "2"
            },
            {
              value: "3"
            }
          ]
        },
        // 
        {
          key: "maxPrice",
          text: "",
          value: "",
          options: [
            {
              value: ""
            },
            {
              value: "$2000"
            },
            {
              value: "$4000"
            },
            {
              value: "$6000"
            }
          ]
        },
        // 
        {
          key: "brand",
          text: "",
          value: "",
          options: [
            {
              value: ""
            },
            {
              value: ""
            },
            {
              value: ""
            },
            {
              value: ""
            }
          ]
        },
        // 
        {
          key: "distance",
          text: "",
          value: "",
          options: [
            {
              value: ""
            },
            {
              value: "10"
            },
            {
              value: "20"
            },
            {
              value: "30"
            }
          ]
        }
      ]
    };
  },
  created() {},
  methods: {
    handleTagsClick(obj) {
      console.log("handleTagsClick obj", obj);
      this.searchTab.curSelectedIndex = obj.index;
    },
    // 
    handleSearchClick() {
      console.log(" handleSearchClick");
      this.$emit("searchBtnClick");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";
.search {
  font-size: 14px;
  color: #fff;
  text-align: center;
  box-sizing: border-box;
  .nav {
    display: flex;
    align-items: center;
    height: 80px;
    line-height: 80px;
    background: $primaryColor;

    .nav-item {
      width: 150px;
      height: 38px;
      line-height: 38px;
      border: 1px solid #fff;
      box-sizing: border-box;

      &:hover {
        background: #fff;
        color: #000;
      }
    }

    .selected {
      position: relative;
      background: #fff;
      color: #000;

      &::after {
        position: absolute;
        content: "";
        bottom: -20px;
        left: 63px;
        width: 0;
        height: 0;
        border-width: 10px 12px;
        border-style: solid;
        border-color: #fff transparent transparent transparent;
      }
    }
  }
  .search-form {
    flex-wrap: wrap;
    padding: 0 20px 15px 20px;
    background: $primaryColor;
    box-sizing: border-box;
  }
}
</style>



