<template>
  <modal @close="handleClose">
    <section class="login-modal">

      <!--  tab  -->
      <div class="title c-font-big">
        <div
          class="tab-sign-in c-btn "
          :class="{
            'selected':currentTab === 0
          }"
          @click="handleTabChange(0)">
          
        </div>
        <div class="divider c-gap-left-small c-gap-right-small">|</div>
        <div
          class="tab-sign-up c-btn"
          :class="{
            'selected':currentTab === 1
          }"
          @click="handleTabChange(1)">
          
        </div>
      </div>

      <!--  -->
      <section class="bottom sign-in">
        <!---->
        <section class="form-item">
          <div class="c-font-big"> / </div>
          <el-input
            class="c-gap-top"
            v-model="form.username"></el-input>
          <div
            v-if="currentTab === 0"
            class="row-right c-gap-top">
            <span class="link c-btn">
              
            </span>
          </div>
        </section>
        <!---->
        <section class="form-item c-gap-top-20">
          <div class="c-font-big"></div>
          <el-input
            class="c-gap-top"
            type="password"
            v-model="form.password">
          </el-input>
          <!--  -->
          <div
            v-if="currentTab === 0"
            class="row-right c-gap-top">
            <span class="link c-btn">
              
            </span>
          </div>
          <!--  -->
          <div
            v-if="currentTab === 1"
            class="c-color-grey c-gap-top">
            (6/)
          </div>
        </section>
        <!---->
        <section
          v-if="currentTab === 1"
          class="form-item c-gap-top-20">
          <div class="c-font-big"></div>
          <el-input
            class="c-gap-top"
            type="password"
            v-model="form.passwordAgain">
          </el-input>
        </section>
        <!--  -->
        <el-button
          v-if="currentTab === 0"
          class="btn-sign-in"
          type="success"
          @click="handleSignIn">
          
        </el-button>
        <!--  -->
        <el-button
          v-if="currentTab === 1"
          class="btn-sign-in"
          type="success"
          @click="handleSignUp">
          
        </el-button>
      </section>
    </section>
  </modal>
</template>

<script>
import Modal from "../../components/Modal/Modal.vue";
import httpConfig from "../../configs/httpConfig";
import axios from "axios";

export default {
  name: "LoginModal",
  props: {
    // 01
    type: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      currentTab: 0,
      form: {
        username: "baijiache2@hotmail.com",
        password: "12345-FDsa",
        passwordAgain: ""
      }
    };
  },
  created() {
    console.log("created", this.type);
    this.currentTab = this.type;
  },
  components: {
    Modal
  },
  methods: {
    handleClose() {
      this.$emit("close");
    },
    handleTabChange(index) {
      console.log(index);
      this.currentTab = index;
    },
    // 
    handleSignIn() {
      console.log(
        " handleSignIn",
        httpConfig.serverHost,
        "username",
        this.form.username,
        "password",
        this.form.password
      );
      // 
      axios
        .post(`${httpConfig.serverHost}/auth/signin`, {
          email: this.form.username,
          password: this.form.password
        })
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
      this.$store.commit("setLoginState", true);
      this.$emit("close");
    },
    // 
    handleSignUp() {
      console.log(" handleSignUp");
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/app";

.login-modal {
  padding: 0 20px 40px;
  width: 500px;
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;

  .title {
    display: flex;
    justify-content: center;

    .tab-sign-in,
    .tab-sign-up {
      padding: 0 10px;
    }

    .selected {
      background: $primaryColor;
      color: #fff;
    }
  }

  .bottom {
    margin-top: 60px;

    .form-item {
      position: relative;

      .row-right {
        position: absolute;
        bottom: -30px;
        right: 0;
        text-align: right;

        .link {
          color: $lightBlue500;
        }
      }
    }

    .btn-sign-in {
      margin-top: 60px;
      width: 100%;
    }
  }
}
</style>



