import Vue from 'vue';
import Vuex from 'vuex';

//  Vuex
Vue.use(Vuex);

//  Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    // 
    isLogin: false
  },
  mutations: {
    /**
     * 
     * @param {*} state
     * @param {*} isLogin
     */
    setLoginState(state, isLogin) {
      state.isLogin = isLogin;
    }
  }
});



